*{box-sizing:border-box}html body{font-family:"Montserrat Alternates", sans-serif}


.MainComponent{overflow:auto}

.Landing-One{min-height:100vh;background:linear-gradient(90deg, #fff 75%, #408af8 25%);color:#20579e;padding-bottom:74px}.Landing-One header{height:60px;position:relative}.Landing-One header span.job-position{position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);text-transform:uppercase;font-size:26px;font-weight:300}.Landing-One main{padding-left:6.5%;padding-right:6.5%;display:flex;justify-content:space-between;position:relative;height:580px}.Landing-One main .mini-description{width:56.1%;padding-top:60px;padding-left:45px}.Landing-One main .mini-description span.desc_name{font-size:40px;font-weight:500;line-height:1.3}.Landing-One main .mini-description p{margin-top:40px;line-height:2}.Landing-One main .block-photo{background-color:#e2e2e2;width:29.89%;z-index:1}.Landing-One main .block-decoration{z-index:0;background:#fff;background-image:radial-gradient(#c3c6cd 2px, transparent 0);background-size:30px 30px;background-position:-5px 0px;position:absolute;width:11%;height:603.5px;left:930px;top:50px}

button.ButtonLink{background-color:transparent;border:2px solid #4e6fa5;border-radius:0.6em;color:#4e6fa5;font-size:14px;line-height:1;padding:1.2em 2.8em;text-align:center;text-transform:uppercase;font-weight:700;transition:box-shadow 300ms ease-in-out, color 300ms ease-in-out}button.ButtonLink:hover,button.ButtonLink:focus{color:#fff;outline:0;box-shadow:0 0 40px 40px #4e6fa5 inset;cursor:pointer}

.Landing-Two{min-height:100vh;padding-bottom:154px}.Landing-Two header{position:relative;height:120px}.Landing-Two header span.span-header-name{position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);font-size:16px;color:#4e6fa5;font-weight:700;line-height:1;text-transform:uppercase}.Landing-Two header p.p-header-text{position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);font-size:16px;margin-top:30px}.Landing-Two main{width:75%;margin-left:auto;margin-right:auto;display:flex;flex-direction:column;position:relative}.Landing-Two main div.cards-portfolio{z-index:1;display:flex;justify-content:space-between;flex-wrap:wrap;height:440px}.Landing-Two main div.cards-portfolio div.card{width:350px;height:195px;background-color:#4089f8}.Landing-Two main .ButtonLink{position:absolute;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%)}.Landing-Two main .block-decoration{z-index:0;background:#fff;background-image:radial-gradient(#c3c6cd 2px, transparent 0);background-size:30px 30px;background-position:-5px 0px;position:absolute;width:11%;height:200px;width:200px;left:-80px;top:-80px}

.Landing-Three{min-height:100vh;padding-bottom:163px;background:linear-gradient(135deg, #408af8 20%, #fff 33%)}.Landing-Three header{height:60px;position:relative}.Landing-Three header span.interests-name{position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);text-transform:uppercase;font-size:16px;font-weight:700;color:#4e6fa5;line-height:1}.Landing-Three main{position:relative;width:80%;margin-left:auto;margin-right:auto}.Landing-Three main div.interests-cards{margin-top:20px;height:470px;display:flex;flex-direction:column;justify-content:space-between}.Landing-Three main div.interests-cards div.row-one{display:flex;justify-content:space-between}.Landing-Three main div.interests-cards div.row-two{display:flex;justify-content:space-between}.Landing-Three main div.interests-cards div.int-card{z-index:1;width:200px;height:200px;border-radius:50%}.Landing-Three main div.interests-cards div.int-card .img-wrapper{overflow:hidden;width:200px;height:200px;border-radius:50%}.Landing-Three main div.interests-cards div.int-card .img-wrapper .int-img{width:200px;height:200px}.Landing-Three main div.interests-cards div.int-card .img-name{text-align:center}.Landing-Three main .ButtonLink{position:absolute;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%)}.Landing-Three main .block-decoration{z-index:0;background:#fff;background-image:radial-gradient(#c3c6cd 2px, transparent 0);background-size:30px 30px;background-position:-5px 0px;position:absolute;width:203.5px;height:203.5px;left:92%;top:55vh}

.Landing-Four{min-height:100vh}.Landing-Four header{height:100px;background-color:#4089f8}.Landing-Four header .Navigation{margin-left:auto;margin-right:auto;width:80%}.Landing-Four main{width:80%;margin-left:auto;margin-right:auto;display:flex;justify-content:space-between;color:#4e6fa5;position:relative}.Landing-Four main .create-email{text-transform:uppercase;width:50%;padding-top:40px}.Landing-Four main .create-email p{text-align:center;text-transform:uppercase;font-size:16px;font-weight:700;color:#4e6fa5;line-height:1}.Landing-Four main .create-email form[name="email-form"]{display:flex;flex-direction:column;width:80%;margin-left:auto;margin-right:auto}.Landing-Four main .create-email form[name="email-form"] input{display:block;margin-top:20px;height:40px;outline:0;border:2px solid #4089f8;border-radius:2%;padding-left:10px;padding-right:10px;padding-top:10px;padding-bottom:10px}.Landing-Four main .create-email form[name="email-form"] textarea{margin-top:20px;outline:0;border:2px solid #4089f8;border-radius:2%;padding-left:10px;padding-right:10px;padding-top:10px;padding-bottom:10px;resize:none;width:100%;height:200px}.Landing-Four main .create-email form[name="email-form"] button{margin-top:20px;outline:0;border:none;background-color:#1b54ab;border-bottom:2px solid #4089f8;color:#fff;padding-top:20px;padding-bottom:20px;transition:box-shadow 300ms ease-in-out, color 300ms ease-in-out}.Landing-Four main .create-email form[name="email-form"] button:hover,.Landing-Four main .create-email form[name="email-form"] button:focus{color:#1b54ab;outline:0;box-shadow:0 0 40px 40px #fff inset}.Landing-Four main .alternative-socials{position:absolute;top:90%;left:46.5%;-webkit-transform:rotate(-90deg);transform:rotate(-90deg);-webkit-transform-origin:top left;transform-origin:top left;color:#4e6fa5;font-size:26px;text-transform:uppercase}.Landing-Four main .social-links{width:50%;padding-top:40px;height:568.4px;display:flex;flex-direction:column;justify-content:space-around}.Landing-Four main .social-links div a{display:flex;text-decoration:none;position:relative}.Landing-Four main .social-links div a img{width:15%}.Landing-Four main .social-links div a span{display:inline-block;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);font-size:26px;font-weight:500}

.Navigation{font-family:"Montserrat Alternates", sans-serif;position:relative;height:100px;background-color:transparent;line-height:normal}.Navigation nav{height:100px;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);text-transform:uppercase;font-size:26px;font-weight:300;width:100%;display:flex;justify-content:space-between;padding-top:35px;padding-bottom:35px}.Navigation nav a{cursor:pointer;display:block;text-decoration:none;color:#fff}.Navigation nav a:hover{background-color:#fff;color:#4089f8}

.Summary-Component header{top:0;left:0;width:100%;display:flex;flex-direction:row;font-family:"Poppins", sans-serif}.Summary-Component header div.Summary-Left{width:60%;background:linear-gradient(90deg, #fff 45%, #f6f6f6 45%);font-weight:700;color:#999999;font-size:100px;line-height:110px}.Summary-Component header div.Summary-Left div.Hello-World{margin-top:25px;margin-left:auto;margin-right:auto;width:530px}.Summary-Component header div.Summary-Right{background:#d8d8d8;height:712px;width:40%}.Summary-Component main{font-family:"Poppins", sans-serif}.Summary-Component main nav.nav{background-color:#4089f8}.Summary-Component main nav.nav .Navigation{margin-left:auto;margin-right:auto;width:85%}.Summary-Component main section{margin-top:100px;width:85%;margin-left:auto;margin-right:auto}.Summary-Component main section h2{font-weight:700;font-size:80px;line-height:120px;color:#999999}.Summary-Component main section.skills article{display:flex}.Summary-Component main section.skills article div.icon-skill{margin-right:30px;margin-left:30px;margin-top:50px}.Summary-Component main section.skills article div.icon-skill p span{display:inline-block;font-size:90px;color:#666666;background-color:#ededed;padding-left:50px;padding-right:50px;padding-top:20px;padding-bottom:20px;border-top-left-radius:50px;border-top-right-radius:50px;border-bottom-left-radius:50px;border-bottom-right-radius:50px}.Summary-Component main section.skills article div.desc-skill{position:relative;width:100%}.Summary-Component main section.skills article div.desc-skill p{position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);font-weight:400;font-size:18px;line-height:33px;letter-spacing:0.39px;color:#6d6d6d}.Summary-Component main section.work article.work-in-history{margin-top:60px;display:flex}.Summary-Component main section.work article.work-in-history div.left-name-age{margin-right:270px}.Summary-Component main section.work article.work-in-history div.left-name-age h3{margin:0;font-weight:500;font-style:normal;font-size:30px;line-height:33px;letter-spacing:0.64px;color:#6d6d6d}.Summary-Component main section.work article.work-in-history div.left-name-age p{margin-top:16.05px;font-weight:500;font-style:normal;font-size:18px;line-height:36px;letter-spacing:0.39px;color:#7e7c7c}.Summary-Component main section.work article.work-in-history div.right-desc-tech p{margin-top:3px;font-weight:400;font-size:18px;line-height:33px;letter-spacing:0.39px;color:#6d6d6d}.Summary-Component main section.work article.work-in-history div.right-desc-tech p:last-child{margin-top:-4px}.Summary-Component main section.work article.work-in-history div.right-desc-tech p:last-child span.tech-label{display:inline-block;font-size:12px;line-height:33px;letter-spacing:0.26px;height:33px;color:#666666;background-color:#ededed;margin-right:16px;padding-left:14px;padding-right:14px;border-radius:16px}.Summary-Component main section.work article.job-search p{margin-top:16.05px;font-weight:500;font-style:normal;font-size:18px;line-height:36px;letter-spacing:0.39px;color:#7e7c7c}

