.Landing-One {
  min-height: 100vh;

  background: linear-gradient(
    90deg,
    rgb(255, 255, 255) 75%,
    rgb(64, 138, 248) 25%
  );
  color: #20579e;
  padding-bottom: 74px;

  header {
    height: 60px;
    position: relative;

    span.job-position {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      text-transform: uppercase;
      font-size: 26px;
      font-weight: 300;
    }
  }

  main {
    padding-left: 6.5%;
    padding-right: 6.5%;
    display: flex;
    justify-content: space-between;
    position: relative;
    height: 580px;

    .mini-description {
      width: 56.1%;
      padding-top: 60px;
      padding-left: 45px;

      span.desc_name {
        font-size: 40px;
        font-weight: 500;
        line-height: 1.3;
      }

      p {
        margin-top: 40px;
        line-height: 2;
      }
    }

    .block-photo {
      background-color: #e2e2e2;
      width: 29.89%;
      z-index: 1;
    }

    .block-decoration {
      z-index: 0;
      background: #fff;
      background-image: radial-gradient(#c3c6cd 2px, transparent 0);
      background-size: 30px 30px;
      background-position: -5px 0px;
      position: absolute;
      width: 11%;
      height: 603.5px;
      left: 930px;
      top: 50px;
    }
  }
}
